
$main : #0077ff;


.제목 {
    color : $main;
}

.my-alert {
    background: #ffe9e9;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}

.my-alert-blue {
    @extend .my-alert;
    background: #a8d9f0;
}

.my-alert p {
    margin-bottom: 0;
}

@mixin test() {
    background: #ffe9e9;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}
.my-alert-test {
   @include test();
}